import React, { useState } from "react";
import { Button } from "@mui/material";

import EnterprisePopUp from "../../components/EnterprisePopUp";
import EnterpriseImg from "../assets/enterprise.svg";

const Enterprise = ({ isMobile, className }) => {
  const [enterpriseModal, setEnterpriseModal] = useState(false),
    openEnterpriseModal = () => setEnterpriseModal(true),
    closeEnterpriseModal = (event, reason) => {
      setEnterpriseModal(false);
    };

  return (
    <section className={className}>
      <div className="content">
        <div className="data">
          <h2 className="title">{"EasySocial for Enterprise"}</h2>
          {isMobile && (
            <div className="image">
              <img
                src={EnterpriseImg}
                alt="Enterprise"
                className="block-image"
              />
            </div>
          )}
          <p className="info">
            {
              "Scalable solutions with robust security and extreme personalization is the key to success for business leaders. We handhold you throughout  and enable solutions that fit best within your exiting workflows with minimum learning curve. Talk to us to understand how we can unify social media to your advantage."
            }
          </p>
          <Button
            variant="contained"
            onClick={openEnterpriseModal}
            className="action"
          >
            {"Get in Touch"}
          </Button>
        </div>
        {!isMobile && (
          <div className="image">
            <img src={EnterpriseImg} alt="Enterprise" className="block-image" />
          </div>
        )}
      </div>
      <EnterprisePopUp open={enterpriseModal} onClose={closeEnterpriseModal} />
    </section>
  );
};

export default Enterprise;
